@import "../Variable/Variable.scss";

.navbar-wrapper {
  background-color: white;
  // background-color: #1e1c1e;
  // position: fixed;
  top: 0;
  width: 100%;
  padding: 1rem 0;
  transition: all 0.5s ease;
  z-index: 1000;

  &.scroll {
    background-color: rgba(0, 0, 0, 0.482);
    padding: 0;
    // padding-top: 60px;
    // position: fixed;
    // margin-bottom: 60px;
  }
  .navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0rem 3rem;
    @media (max-width: 1000px) {
      padding: 0 0.5rem;
    }

    .logo {
      width: 8rem;
      height: 5rem;
      transition: all ease 0.5s;
      @media (max-width: 1000px) {
        width: 4rem;
      }
    }

    .lists {
      .list {
        list-style-type: none;
        display: flex;
        gap: 2rem;
        @media (max-width: 1000px) {
          display: none;
        }

        li {
          .link {
            color: black;
            font-size: 1.2rem;
            position: relative;

            .sublinks {
              border-top: 2px solid $main;
              background-color: white;
              width: max-content;
              color: black;
              box-shadow: 0px 0px 40px -20px rgb(0, 0, 0);
              display: flex;
              left: 0;
              flex-direction: column;
              position: absolute;
              top: 100%;
              z-index: 11;
              list-style-type: none;
              visibility: hidden;
              transform: scaleY(0);
              transform-origin: 0 0 0;
              opacity: 0;
              transition: all 0.5s ease;

              li {
                padding: 0.5rem 1rem;
                font-weight: 400;
                border-bottom: solid 1px #e9e9e9;

                &:hover {
                  background-color: $main;
                  color: white;
                }
              }
            }

            .sublinks.active {
              transform: scaleY(1);
              opacity: 1;
              visibility: visible;
            }

            &:hover {
              color: $main;
            }

            &.active {
              color: $main;
            }
          }
        }

        &.home {
          li {
            .link {
              color: black;
              &:hover {
                color: $main;
              }

              &.active {
                color: $main;
              }
            }
          }
        }
      }

      svg {
        color: white;
        font-size: 2rem;
        display: none;

        @media (max-width: 1000px) {
          display: flex;
        }
        @media (max-width: 500px) {
          font-size: 1.5rem;
        }
      }
    }
  }
}

.nav-mob-res {
  position: absolute;
  background-color: white;
  width: 100%;
  display: none;
  z-index: 100;
  @media (max-width: 1000px) {
    display: block;
  }
  .mob-nav {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.5s linear;
    &.active {
      max-height: 60rem;
      opacity: 1;
    }

    a {
      li {
        padding: 0.5rem 1rem;
        border-bottom: 2px solid #eee;
        &:hover {
          background-color: $main;
          color: white;
        }
      }
      .mob-sub-list {
        padding-left: 3rem;
        list-style: none;
        max-height: 0;
        overflow: hidden;
        transition: max-height 0.5s linear;
        &.active {
          max-height: 60rem;
          opacity: 1;
        }
      }

      &.active {
        background-color: $main;
        color: white;
      }
    }

